<script>
	// Imports
	import language from "../language.js";
	import currencies from "../currencies.js";
	import { sendLang, receiveIso, receiveCurnc } from "../stores.js";

	// Exports
	export let openDropdown;
	export let resetReceiver;
	export let optimusAll;

	// Props
	let searchTerm = "";
	let countryFilter = [];
	let searchInput = "";

	$: if(openDropdown) {
		searchTerm = null
		runFilter();
	}

	// Function to create the new object
	const countrySpreadArray = [];
	const countrySpread = (optimusAll, $currencies) => {
		for (const code in optimusAll) {
			const countryName = optimusAll[code];
			const currencyInfo = $currencies.find(
				(item) => item.countryiso === code
			);
			if (currencyInfo) {
				countrySpreadArray.push({
					countryiso: code,
					countryname: countryName,
					currencies: currencyInfo.currencies
				});
			}
		}
		return countrySpreadArray;
	}
	// Create the new object
	$: countryJoined = countrySpread(optimusAll, $currencies);


	// Filter countries
	const runFilter = () => {
		if (!searchTerm) {
			countryFilter = countryJoined;
			return;
		}
		const removeDiacritics = (str) => str.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
		const normalizedSearchTerm = removeDiacritics(searchTerm.trim().toLowerCase());

		const filteredCountries = countryJoined.filter((country) => {
			const normalizedCountryName = removeDiacritics(country.countryname.toLowerCase());
			return normalizedCountryName.includes(normalizedSearchTerm);
		});
		countryFilter = filteredCountries;
	};
</script>



<!-- svelte-ignore a11y-no-static-element-interactions -->
<div class:selected={openDropdown} id="dropdownmenu">
	<span>
		<input
			bind:this={searchInput}
			bind:value={searchTerm}
			on:input={() => runFilter()}
			type="search"
			id="search"
			name="search"
			placeholder={$language[$sendLang].searchterm}
			autocomplete="off"
			inputmode="text"
		/>
		<button type="button" on:click={() => openDropdown = false}>
			<iconify-icon icon="iconamoon:close" width="24" height="24"></iconify-icon>
		</button>
	</span>
	<menu>
		{#if countryFilter}
			{#each countryFilter.sort((a, b) => a.countryname.localeCompare(b.countryname)) as countryObject}
				{#each countryObject.currencies as curr (curr.currencyiso)}
					<li class:checked={countryObject.countryiso === $receiveIso && curr.currencyiso === $receiveCurnc}>
						<iconify-icon icon="circle-flags:{countryObject.countryiso.toLowerCase()}" width="28" height="28"></iconify-icon>
						<input
							type="button"
							name="country"
							id={countryObject.countryiso + curr.currencyiso}
							value={countryObject.countryname}
							on:click|stopPropagation={() => resetReceiver(countryObject.countryiso, curr.currencyiso)}
							on:click|stopPropagation={() => openDropdown = false}
							on:click|stopPropagation={() => $receiveCurnc = curr.currencyiso}
							on:click|stopPropagation={() => $receiveIso = countryObject.countryiso}
						/>
						<label for={countryObject.countryiso + curr.currencyiso}>
							{curr.currencyiso}
						</label>
					</li>
				{/each}
			{/each}
		{/if}
	</menu>
</div>

