<script>
	// Imports
	import language from "../language.js";
	import {
		sendLang,
		sendAmount,
		receiveCurnc,
		sendCurnc,
        promise,
		payinSelected,
		payoutSelected,
		serviceSelected,
		payins,
		payouts
	} from "../stores.js";

	// Exports
	export let disclaimerfx;
	export let disclaimerfee;
	export let hrefLocation;
	export let formatNumber;
	export let parseAmount;

	// Show or hide dropdown
	const toggleDropdown = (type) => {
		if (type === "payins") {
			$payins = true;
			$payouts = false;
		} else if (type === "payouts") {
			$payins = false;
			$payouts = true;
		}
		const handleMenuClose = (e) => {
			const dropdownElement = document.querySelector("form");
			if (!dropdownElement.contains(e.target)) {
				$payins = false;
				$payouts = false;
				disclaimerfx = false;
				disclaimerfee = false;
				document.body.removeEventListener("click", handleMenuClose);
			}
		};
		document.body.addEventListener("click", handleMenuClose);
	};
	const handleButtonClick = (event) => toggleDropdown(event, event);
</script>

{#await $promise}
	<div class="loading">
		<table></table>
	</div>
{:then _}
	<div>
		<table>
			<!-- Payment method -->
			<tr>
				<th>
					<iconify-icon icon="iconamoon:arrow-top-right-1-bold" width="14" height="14"></iconify-icon>
					{$language[$sendLang].tablesend}
				</th>
				<td>
					<input
						type="button"
						id="payins"
						name="payins"
						value={$language[$sendLang][$payinSelected]}
						on:click|stopPropagation={() => handleButtonClick("payins")}
					/>
				</td>
			</tr>
			<!-- Receive method -->
			<tr>
				<th>
					<iconify-icon icon="iconamoon:arrow-bottom-right-1-bold" width="14" height="14"></iconify-icon>
					{$language[$sendLang].tablereceive}
				</th>
				<td>
					<input
						type="button"
						id="payouts"
						name="payouts"
						value={$language[$sendLang][$payoutSelected]}
						on:click|stopPropagation={() => handleButtonClick("payouts")}
					/>
				</td>
			</tr>
			<!-- Exchange Rate -->
			<tr>
				<th>
					<iconify-icon icon="iconamoon:close-bold" width="14" height="14"></iconify-icon>
					<button on:click={() => disclaimerfx = true}>
						{$language[$sendLang].tablerate}
					</button>
				</th>
				<td>
					{#if $serviceSelected.exchangeRate}
						{formatNumber($serviceSelected.exchangeRate, 4)}
						{$receiveCurnc}
					{/if}
				</td>
			</tr>
			<!-- Transfer fee -->
			<tr>
				<th>
					<iconify-icon icon="iconamoon:sign-plus-bold" width="14" height="14"></iconify-icon>
					<button on:click={() => disclaimerfee = true}>
						{$language[$sendLang].tablefee}
					</button>
				</th>
				<td>
					<a href={hrefLocation} class:special={$serviceSelected.grossFee === 0}>
						{formatNumber($serviceSelected.grossFee, 2)}
						{$sendCurnc}
					</a>
				</td>
			</tr>
			<!-- Transfer total -->
			<tr>
				<th>
					<iconify-icon icon="iconamoon:sign-equal-bold" width="14" height="14"></iconify-icon>
					{$language[$sendLang].tablepay}
				</th>
				<td>
					{formatNumber(parseFloat(parseAmount($sendAmount)) + parseFloat($serviceSelected.grossFee), 2)}
					{$sendCurnc}
				</td>
			</tr>
		</table>
	</div>
{/await}

