<script>
	// Imports
	import language from "../language.js";
	import {
		receiveIso,
		receiveCurnc,
		receiveAmount,
		hostName,
		sendIso,
		sendLang,
		sendCurnc,
		sendAmount,
		serviceGroup,
		isLoading,
		promise,
		payinPromo,
		payoutPromo,
		uuidv4,
		serviceSelected,
		payinSelected,
		payoutSelected,
		err
	} from "../stores.js";

	// Exports
	export let optimusAll = [];
	export let disclaimer = false;
	export let openDropdown;
	export let formatNumber;
	export let parseAmount;

	// Props
	let emptyInput = false;

	// Props
	const pricesProducts = "/wuconnect/prices/products";
	// const pricesLimits= "/wuconnect/prices/limits";

	// Fetch product list
	export const fetchProducts = async () => {
		if (!$sendAmount || $sendAmount === 0) {
			emptyInput = true;
		} else {
			try {
				const bodyProducts = {
					origination: {
						country: $sendIso,
						currency: $sendCurnc,
						// US format — sendAmount: parseFloat($sendAmount.replace(',', '').replace(',', '.')),
						// EU format - sendAmount: parseFloat($sendAmount.replace('.', '').replace(',', '.')),
						sendAmount: parseFloat($sendAmount.replace(',', '').replace(',', '.')),
					},
					destination: {
						country: $receiveIso,
						currency: $receiveCurnc,
					},
				};
				const response = await fetch($hostName + pricesProducts, {
					method: "POST",
					headers: {
						"Content-Type": "application/json",
						"x-api-key": "7bb8938a-bfeb-40a4-8a11-e702de87d855",
						"x-wu-externalRefId": $uuidv4,
					},
					body: JSON.stringify(bodyProducts),
				});
				if (!response.ok) {
					throw new Error(`HTTP error! status: ${response.status}`);
				}
				const data = await response.json();
				$serviceGroup = data.products;
				let highestRate = 0;
				// Do the calculations
				$serviceGroup.forEach((item) => {
					switch (true) {
						// Default - best exchange rate and fee
						case item.exchangeRate > highestRate &&
							item.grossFee <= highestRate:
							highestRate = item.exchangeRate;
							$serviceSelected = item;
							$payinSelected = $serviceSelected.payin;
							$payoutSelected = $serviceSelected.payout;
							break;
						// If pay in set
						case ($payinPromo && !$payoutPromo) && 
							item.payin === $payinPromo:
							$serviceSelected = item;
							$payinSelected = $serviceSelected.payin;
							$payoutSelected = $serviceSelected.payout;
							break;
						// If pay out set
						case ($payoutPromo && !$payinPromo) && 
							item.payout === $payoutPromo:
							$serviceSelected = item;
							$payinSelected = $serviceSelected.payin;
							$payoutSelected = $serviceSelected.payout;
							break;
						// If pay in & pay out set
						case ($payinPromo && $payoutPromo) && 
							item.payout === $payoutPromo &&
							item.payin === $payinPromo:
							$serviceSelected = item;
							$payinSelected = $serviceSelected.payin;
							$payoutSelected = $serviceSelected.payout;
							break;
					}
				});
				$err = false;
			} catch (error) {
				$err = true;
				throw new Error(error);
			}
		}
	};

	// Fetch limits list
	export const fetchLimits = async () => {
		try {
			const bodyProducts = {
				origination: {
					country: $sendIso,
					currency: $sendCurnc,
					sendAmount: $sendAmount,
				},
				destination: {
					country: $receiveIso,
					currency: $receiveCurnc,
				},
			};
			const response = await fetch($hostName + pricesLimits, {
				method: "POST",
				headers: {
					"Content-Type": "application/json",
					"x-api-key": "7bb8938a-bfeb-40a4-8a11-e702de87d855",
					"x-wu-externalRefId": $uuidv4,
				},
				body: JSON.stringify(bodyProducts),
			});
			if (!response.ok) {
				throw new Error(`HTTP error! status: ${response.status}`);
			}
			const data = await response.json();
			console.log("data limits", data.products)
			for(const sfsf of data.products) {
				console.log("data limits", sfsf.payout, sfsf.payin, sfsf.maxLimit)
			}
		} catch (error) {
			$err = true;
			throw new Error(error);
		}
	};

	// Get local coutry names and replace main list country names
	export const fetchOptimus = async () => {
		const bodyCountries = {
			origination: $sendIso,
			language: $sendLang,
			lists: [
				{
					name: "COUNTRY_LIST",
					type: "country",
				},
				{
					name: "TOP_COUNTRIES",
					type: "country",
				},
			],
			bashPath: `/${$sendIso}/${$sendLang}`,
		};
		try {
			const response = await fetch(
				$hostName +
					"/wuconnect/dcaas/list.optimus.json?dcaasversion=R231220",
				{
					method: "POST",
					headers: {
						"Content-Type": "application/json",
						Accept: "application/json",
					},
					body: JSON.stringify(bodyCountries),
				},
			);
			const data = await response.json();
			// Populate country list
			optimusAll = data.COUNTRY_LIST;
			$err = false;
		} catch (error) {
			$err = true;
			throw new Error(error);
		}
	};
	
	// Handle calculation and number validation
	export const handleInput = (amount, option) => {
		// Check if input is empty or zero
		if (amount === 0 || amount === "") {
			emptyInput = true;
			return;
		}
		emptyInput = false;
		// Parse the amount
		if (option === "sending") {
			$receiveAmount = parseAmount($sendAmount) * $serviceSelected.exchangeRate;
			$receiveAmount = formatNumber($receiveAmount, 2);
		} else if (option === "receiving") {
			$sendAmount = parseAmount($receiveAmount) / $serviceSelected.exchangeRate;
			$sendAmount = formatNumber($sendAmount, 2);
		}
	};

	// Show or hide dropdown
	const handleButtonClick = () => {
		openDropdown = true;
		disclaimer = false;
		const handleMenuClose = (event) => {
			const dropdownElement = document.querySelector("form");
			if (!dropdownElement.contains(event.target)) {
				openDropdown = false;
				disclaimer = false;
				document.body.removeEventListener("click", handleMenuClose);
			}
		};
		document.body.addEventListener("click", handleMenuClose);
	};

	// Init fetch and calculation
	$promise = fetchProducts().then(() => fetchOptimus()).then(() => handleInput($sendAmount, "sending"));
	$sendAmount = formatNumber($sendAmount, 2);


	$: console.log("serviceGroup", $serviceGroup)
</script>

{#await $promise}
	<fieldset><input type="text" class="loading"/></fieldset>
	<!-- Summary -->
	<slot></slot>
	<fieldset><input type="text" class="loading"/></fieldset>
{:then}
	<!-- Sending -->
	<fieldset>
		<label for="sending">{$language[$sendLang].sendlabel}</label>
		<input
			type="text"
			id="sending"
			name="sending"
			step="0.01"
			autocomplete="off"
			inputmode="decimal"
			bind:value={$sendAmount}
			on:keyup|stopPropagation={() => handleInput($sendAmount, "sending")}
			on:change|stopPropagation={() => (fetchProducts(), handleInput($receiveAmount, "receiving"))}
			aria-label={$language[$sendLang].sendlabel}
		/>
		<figure style="cursor: not-allowed;" title="Locked">
			<iconify-icon icon="circle-flags:{$sendIso.toLowerCase()}" width="30" height="30"></iconify-icon>
			<!-- svelte-ignore a11y-structure -->
			<p>{$sendCurnc}</p>
			<iconify-icon icon="iconamoon:lock-fill" width="26" height="20"></iconify-icon>
		</figure>
	</fieldset>
	<!-- Summary -->
	{#if emptyInput}
		<section>
			<p>{$language[$sendLang].errempty}</p>
		</section>
	{:else}
		<slot />
	{/if}
	<!-- Receiving -->
	<fieldset>
		<label for="receiving">{$language[$sendLang].receivelabel}</label>
		<input
			type="text"
			id="receiving"
			name="receiving"
			step="0.01"
			autocomplete="off"
			inputmode="decimal"
			bind:value={$receiveAmount}
			on:keyup|stopPropagation={() => handleInput($receiveAmount, "receiving")}
			on:change|stopPropagation={() => (fetchProducts(), handleInput($sendAmount, "sending"))}
			aria-label={$language[$sendLang].receivelabel}
		/>
		<button on:click|stopPropagation={() => handleButtonClick()}>
			{#if $isLoading}
				<iconify-icon
					icon="svg-spinners:ring-resize"
					width="30"
					height="30"
					observer="false"
				></iconify-icon>
			{:else}
				{#if $receiveIso === 'K1'}
					<iconify-icon icon="circle-flags:xk" width="30" height="30"></iconify-icon>
				{:else if $receiveIso === 'TP'}
					<iconify-icon icon="circle-flags:tl" width="30" height="30"></iconify-icon>
				{:else if $receiveIso === 'C2'}
					<iconify-icon icon="circle-flags:cy" width="30" height="30"></iconify-icon>
				{:else}
					<iconify-icon icon="circle-flags:{$receiveIso.toLowerCase()}" width="30" height="30"></iconify-icon>
				{/if}
			{/if}
			{$receiveCurnc}
			<iconify-icon icon="iconamoon:arrow-down-2" width="26" height="26" observer="false"></iconify-icon>
		</button>
	</fieldset>
{/await}

<style>
	fieldset,
	input,
	label,
	figure,
	button,
	iconify-icon {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
	}
	/* Inputs */
	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
		-webkit-appearance: none;
		appearance: none;
		margin: 0;
	}
	fieldset {
		position: relative;
		width: 100%;
		border: none;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		align-content: flex-start;
	}
	fieldset:focus-within label {
		color: var(--dark);
		transition: all 0.15s ease-out;
	}
	label {
		position: absolute;
		top: 0;
		left: calc(var(--remmy) / 1.4);
		padding: 0 calc(var(--remmy) / 2);
		height: calc(var(--remmy) * 2);
		font-size: calc(var(--remmy) / 1.1);
		color: var(--grey);
		display: flex;
		align-items: flex-end;
		letter-spacing: 0.5px;
		cursor: text;
	}
	input {
		height: 100%;
		width: 100%;
		background-color: white;
		transition: all 0.15s ease-out;
		border-radius: var(--rad);
		border: 1px solid var(--greyish);
		outline: 0px solid transparent;
		outline-offset: 0px;
		font-family:"Euclid WU","Noto Sans",sans-serif;
		font-weight: 600;
		color: var(--dark);
		text-align: left;
	}
	input[type="text"] {
		padding: 0 calc(var(--remmy) * 1.2);
		padding-top: calc(var(--remmy) * 1.2);
		min-height: calc(var(--remmy) * 4.6);
		max-height: calc(var(--remmy) * 4.6);
		font-size: calc(var(--remmy) * 1.4);
		letter-spacing: 1px;
	}
	input:focus {
		outline: 3px solid var(--dark);
		outline-offset: -3px;
	}
	input::-webkit-contacts-auto-fill-button {
		visibility: hidden;
		pointer-events: none;
		position: absolute;
	}
	figure, button {
		display: flex;
		align-items: center;
		align-content: center;
		justify-content: space-between;
		position: absolute;
		right: calc(var(--remmy) * 1.2);
		border: none;
		background: none;
		color: var(--dark);
		gap: calc(var(--remmy) / 1.6);
		height: 100%;
		text-align: right;
		font-family:"Euclid WU","Noto Sans",sans-serif;
		font-weight: 600;
		font-size: calc(var(--remmy) * 1.1);
	}
	iconify-icon:first-of-type {
		margin-right: calc(var(--remmy) / 4);
		border: 1px solid var(--light);
		background-color: var(--ghost);
		border-radius: 100px;
	}
	section {
		box-sizing: border-box;
		margin: 0;
		padding: 0 calc(var(--remmy) * 1.2);
		display: flex;
		align-items: center;
		align-content: center;
		justify-content: flex-start;
		min-height: calc(var(--remmy) * 3.4);
		width: 100%;
		border-radius: var(--rad);
		background-color: var(--red);
		color: white;
	}
	button {
		cursor: pointer;
	}
</style>
