<script>
	// Imports
	import Icons from "../Icons/Icons.svelte";
	import language from "../language.js";
	import {
		sendLang,
		sendAmount,
		sendCurnc,
		sendIso,
		receiveCurnc,
		serviceGroup,
		payinSelected,
		payoutSelected,
		serviceSelected,
		payins,
		payouts
	} from "../stores.js";

	// Props
	export let handleInput;
	export let disclaimerfee;
	export let disclaimerfx;
	export let serviceGroupPayin;
	export let serviceGroupPayout;

	// Map translations to methods
	$: serviceGroupPayin = $serviceSelected ? [...new Map($serviceGroup.filter(v => v.service === $serviceSelected.payout).map(v => [v.payin, v])).values()] : [];
	
	$: serviceGroupPayout = $serviceSelected ? [...new Map($serviceGroup.filter(v => v.payin === $serviceSelected.payin).map(v => [v.service, v])).values()] : [];

	const mergeEverything = async() => {
		[$serviceSelected] = $serviceGroup.filter(group => group.payin === $payinSelected && group.service === $payoutSelected);
	}

	// Format numbers
	const formatNumber = (value, digits) => {
		return new Intl.NumberFormat(`${$sendLang.toLowerCase()}-${$sendIso}`, {
			style: "decimal",
			minimumFractionDigits: digits,
			maximumFractionDigits: digits
		}).format(value);
	};

	$: console.log("serviceGroupPayout", serviceGroupPayout);
	$: console.log("serviceGroupPayin", serviceGroupPayin);
</script>

<div class:selected={$payins || $payouts || disclaimerfee || disclaimerfx}>
	<Icons />
	<header>
		{#if $payins}
			<h4>{@html $language[$sendLang].tablesend}</h4>
		{:else if $payouts}
			<h4>{@html $language[$sendLang].tablereceive}</h4>
		{:else if disclaimerfee}
			<h4>{@html $language[$sendLang].tablefee}</h4>
		{:else if disclaimerfx}
			<h4>{@html $language[$sendLang].tablerate}</h4>
		{/if}
		<button type="button" on:click={() => ($payins = false, $payouts = false, disclaimerfee = false, disclaimerfx = false)}>
			<iconify-icon icon="iconamoon:close" width="24" height="24"></iconify-icon>
		</button>
	</header>
	{#if $payins}
		<ul>
			{#each serviceGroupPayin.sort((a, b) => a.payin.localeCompare(b.payin)) as pin (pin.payin)}
				<li>
					<input type="radio" id={"payin" + pin.payin} value={pin.payin} bind:group={$payinSelected} on:change={() => (mergeEverything(), handleInput($sendAmount, "sending"), $payins = false)}>
					<label for={"payin" + pin.payin}>
						<svg height="120" width="180"><use xlink:href="#{pin.payin}"></use></svg>
						<span>
							{$language[$sendLang][pin.payin]}
							<small>
								{pin.grossFee} {$sendCurnc} {$language[$sendLang].tablefee}
							</small>
						</span>
					</label>
				</li>
			{/each}
		</ul>
	{/if}
	{#if $payouts}
		<ul>
			{#each serviceGroupPayout as pout (pout.service)}
				<li>
					<input type="radio" id={"payout" + pout.service} value={pout.service} bind:group={$payoutSelected} on:change={() => (mergeEverything(), handleInput($sendAmount, "sending"), $payouts = false)}>
					<label for={"payout" + pout.service}>
						<svg height="120" width="180"><use xlink:href="#{pout.service}"></use></svg>
						<span>
							{$language[$sendLang][pout.service]}
							<small>
								1 {$sendCurnc} = {formatNumber(pout.exchangeRate, 4)} {$receiveCurnc}
							</small>
						</span>
					</label>
				</li>
			{/each}
		</ul>
	{/if}
	{#if disclaimerfee}
		<p>
			{@html $language[$sendLang].claimfee}
		</p>
	{/if}
	{#if disclaimerfx}
		<p>
			{@html $language[$sendLang].claimfx}
		</p>
	{/if}
</div>

