<script>
	// Imports
	import "iconify-icon";
	import Inputs from "./PriceAPI/Inputs.svelte";
	import Dropdown from "./PriceAPI/Dropdown.svelte";
	import Summary from "./PriceAPI/Summary.svelte";
	import Fifo from "./PriceAPI/Fifo.svelte";
	import language from "./language.js";
	import fifocodes from "./fifocodes.js";
	import { onMount } from 'svelte';
	import {
		receiveIso,
		receiveCurnc,
		hostName,
		sendLang,
		sendIso,
		sendAmount,
        promise,
		isLoading,
		payinSelected,
		payoutSelected,
		uuidv4,
		err
	} from "./stores.js";

	// Props
	let payoutCode = "";
	let payinCode = "";
	let disclaimerfee;
	let disclaimerfx;

	// Exports
	export let openDropdown = false;
	export let optimusAll;
	export let fetchProducts;
	export let handleInput;
	export let serviceGroupPayin;
	export let serviceGroupPayout;
	export let hrefLocation;

	// Code mapping (pay in remove spaces)
	$: payinCode = $fifocodes.payinCodes[$payinSelected]?.replace(/\s+/g, '');
	$: payoutCode = $fifocodes.payoutCodes[$payoutSelected];

	// Set or update the cookie
	$: hrefLocation = `${$hostName}/${$sendIso?.toLowerCase()}/${$sendLang?.toLowerCase()}/web/user/register`;
	const setDefaults = ($receiveIso, $receiveCurnc, payinCode, payoutCode, $sendAmount) => {
		const cookies = [
			{name: "lastCountry", value: $receiveIso},
			{name: "lastCurrency", value: $receiveCurnc},
			{name: "lastFundsIn", value: payinCode},
			{name: "lastFundsOut", value: payoutCode},
			{name: "lastSendAmount", value: parseAmount($sendAmount)},
			{name: "userIdentity", value: "newCustomer"}
		];
		cookies.forEach(cookie => {
			const cookieExists = document.cookie.split('; ').some(c => c.startsWith(cookie.name + '='));
			const value = encodeURIComponent(cookie.value);
			document.cookie = cookieExists ? `${cookie.name}=${value}; path=/; max-age=3600` : `${cookie.name}=${value}; path=/; max-age=3600; expires=${new Date(Date.now() + 3600e3).toUTCString()}`;
		});
	}

	// Set new receiver props values
	export const resetReceiver = async(countryiso, currencyiso) => {
		try {
			isLoading.set(true);
			receiveIso.set(countryiso);
			receiveCurnc.set(currencyiso);
			await fetchProducts().then(() => handleInput($sendAmount, "sending"));
			isLoading.set(false);
		} catch (error) {
			console.error(error);
		}
	}; 

	// Click vibration
	const vibrateClick = () => {
		if ("vibrate" in navigator || window.navigator.vibrate) {
			window.navigator.vibrate(50)
		}
		setDefaults($receiveIso, $receiveCurnc, payinCode, payoutCode, $sendAmount);
	}

	// Generate random user id
	export const uuidv4Generate = () => 
	'xxxxxxxx-xxxx-xxxx-xxxx-xxxxxxxxxxxxxxxx'.replace(/[xy]/g, c => 
		(c=='x' ? Math.random()*16|0 : (Math.random()*4|8)).toString(16));
	$uuidv4 = uuidv4Generate();

	// Re-fetch each X time
	onMount(() => {
		let sessionDuration = 600000;
		const timeoutId = setInterval(() => 
			$promise = fetchProducts().then(() => handleInput($sendAmount, "sending")), sessionDuration);
		return () => clearInterval(timeoutId);
	});

	// Format number
	export const formatNumber = (value, decimalpoint) => {
		return new Intl.NumberFormat(`${$sendLang.toLowerCase()}-${$sendIso}`, {
			style: "decimal",
			minimumFractionDigits: decimalpoint,
			maximumFractionDigits: decimalpoint
		}).format(value);
	};

	// // Parse number
	// export const parseAmount = (value) => {
	// 	// Remove spaces
	// 	value = value.replace(/\s+/g, '');

	// 	// Remove thousands separators
	// 	if ($localeIso === "US") {
	// 		value = value.replace(/[,]/g, '');
	// 	} else if ($localeIso === "EU") {
	// 		value = value.replace(/[.]/g, '');
	// 		// value = value.replace(/[,](?=[0-9]{2}$)/, '.');
	// 	}

	// 	// Replace decimal separator
	// 	// value = value.replace(/[.](?=[0-9]{2}$)/, '.');

	// 	// Convert to a float and format it
	// 	const number = parseFloat(value);
	// 	// return number;
	// 	return number.toFixed(2);
	// };

	// Parse number
	export const parseAmount = (input) => {
		// Trim whitespace from the input
		input = input.replace(/\s+/g, '');

		let usCurrencyRegex = /^\d{1,3}(,\d{3})*\.\d{2}$/;

		let number;

		if (usCurrencyRegex.test(input)) {
			number = input.replace(/,/g, '');
		} else {
			number = input.replace(/\./g, '').replace(',', '.');
		}

		// Convert to a number and return it in standard format
		return parseFloat(number).toFixed(2);
	};
</script>

<div id="dynamocalc">
	<form on:submit|preventDefault>
		<Inputs bind:optimusAll bind:fetchProducts bind:handleInput bind:openDropdown {formatNumber} {parseAmount} >
			{#if $err}
				<section>
					<p>{$language[$sendLang].errcountry}</p>
				</section>
			{:else}
				<Summary bind:disclaimerfee bind:disclaimerfx {hrefLocation} {formatNumber} {parseAmount} />
			{/if}
		</Inputs>
		<Dropdown bind:openDropdown {optimusAll} {resetReceiver} />
		<Fifo bind:handleInput bind:disclaimerfee bind:disclaimerfx bind:serviceGroupPayin bind:serviceGroupPayout />
		{#await $promise}
			<!-- svelte-ignore a11y-invalid-attribute -->
			<a href="#" class="loading" style="color: #a0a0a0;">&nbsp;</a>
		{:then}
			<a href={hrefLocation} on:click={() => vibrateClick()}>{$language[$sendLang].cta}</a>
		{/await}
	</form>
</div>

